@import url(https://cdn.jsdelivr.net/npm/reseter.css);
@import url(https://fonts.googleapis.com/css2?family=M+PLUS+Code+Latin:wght@100;200&family=Roboto+Mono:wght@100&display=swap);
html{background-color:#18191a}.AppHeader{text-align:center;font-family:"M PLUS Code Latin", sans-serif;color:whitesmoke}.AppHeader p{font-size:1.5rem;margin:2% 0 0 0}.block{display:flex;flex-direction:column;width:20rem}.block div{display:inline-flex}*{box-sizing:border-box}body{background:#18191a}.centered{width:300px;height:60px;margin:5% auto}.group{width:100%;height:60px;overflow:hidden;position:relative}label{position:absolute;top:16.66667px;color:rgba(255,255,255,0.5);font:400 18.75px "Roboto Mono",monospace;cursor:text;transition:0.25s ease-in-out}input{display:block;width:100%;padding-top:18.75px;border:none;border-radius:0;color:#fff;background:#18191a;font-size:20px;font-family:"Roboto Mono", monospace;transition:0.3s ease}input:valid ~ label{top:0;font:700 12px "Roboto Mono",monospace;color:rgba(255,255,255,0.5)}input:focus{outline:none}input:focus ~ label{top:0;font:600 12px "Roboto Mono",monospace;color:#9fa4d0}input:focus ~ .bar:before{transform:translateX(0)}input:-webkit-autofill{-webkit-box-shadow:0 0 0px 1000px #18191a inset;-webkit-text-fill-color:white !important}.bar{background:rgba(255,255,255,0.5);content:"";width:300px;height:2px;transition:0.3s ease;position:relative}.bar:before{content:"";position:absolute;width:100%;height:150%;background:#9fa4d0;transition:0.5s;transform:translateX(-100%)}::selection{background:rgba(159,164,208,0.3)}.exchange{font-family:"M PLUS Code Latin", sans-serif;color:#8875cb;font-size:35px;height:100px;width:350px;text-align:center;margin:0 auto;transition:0.2s}.exchange-active{visibility:visible}

